import dyDatePicker from '../../common/dy-Date.vue'
import {
	debounce
} from '@/common/utils.js'
export default {
	data() {
		return {
			n: true,
			fil_count: '0.000000',
			fil_counts:'0.00',
			trans:'0.00',
			num: '0.0000',
			ber: '0.00',
			nuber: '0.00',
			fee: '',
			month_profit: '0',
			numm: '',
			tabCurrentIndex: 0,
			add_item: '',
			profit: '0',
			entin: [{}],
			flag: false,
			ention: '',
			//控制下拉列表的显示隐藏，false隐藏、true显示
			index: 0,
			//选择的下拉列表下标
			navList: [{
					state: 0,
					text: '收入'
				},
				{
					state: 1,
					text: '支出'
				}
			],
			date: '本月',
			date1: '本月',
			teran: '',
			scrollTop: 0,
			old: {
				scrollTop: 0,
			},
		}
	},
	components: {
		dyDatePicker
	},
	onShow: function(opetions) {
		var that = this
		var data = new Date()
		var text = data.getFullYear('-')
		var txt = data.getMonth() + 1
		var teran = text + '-' + txt
		that.teran = teran
		uni.request({
			url: this.url + "assets/",
			method: 'GET',
			header: {
				Authorization: 'JWT' + ' ' + this.global_.token
			},
			success(res) {
				that.fil_count = res.data.fil_count
				that.fil_counts = parseFloat(res.data.fil_count).toFixed(2)
				that.ber = parseFloat(res.data.availed_num).toFixed(2)
				that.nuber = parseFloat(res.data.locked_num).toFixed(2)
				that.fee = res.data.fee
			}
		})
		// 这是收入记录请求API
		uni.request({
			url: this.url + 'assets/month/profit/',
			method: 'GET',
			header: {
				Authorization: 'JWT' + ' ' + this.global_.token
			},
			data: {
				month: teran
			},
			success(res) {
				var seront = res.data.data
				var ention = res.data.data.profit_records.reverse()
				that.ention = ention
				if (ention.length == 0) {
					that.flag = true
				} else {
					that.flag = false
				}
				that.month_profit = seront.month_profit
			}
		})
	},
	methods: {
		scroll(e) {
			this.old.scrollTop = e.detail.scrollTop
		},
		next: function() {
			this.n = false
		},
		back: function() {
			this.n = true
		},
		tabClick: function(index) {
			var that = this
			var data = new Date()
			var text = data.getFullYear('-')
			var txt = data.getMonth() + 1
			var teran = text + '-' + txt
			that.teran = teran
			if (this.tabCurrentIndex === index) {
				return false
			} else {
				that.tabCurrentIndex = index
			}
			if (this.tabCurrentIndex === 0) {
				that.date = "本月";
				uni.request({
					url: this.url + 'assets/month/profit/',
					method: 'GET',
					header: {
						Authorization: 'JWT' + ' ' + this.global_.token
					},
					data: {
						month: teran
					},
					success(res) {
						var seront = res.data.data
						var ention = res.data.data.profit_records.reverse()
						that.ention = ention
						if (ention.length == 0) {
							that.flag = true
						} else {
							that.flag = false
						}
						that.month_profit = seront.month_profit
					}
				})
			}
			if (this.tabCurrentIndex === 1) {
				that.date1 = "本月";
				uni.request({
					url: this.url + 'assets/month/bill/',
					method: 'GET',
					header: {
						Authorization: 'JWT' + ' ' + this.global_.token
					},
					data: {
						month: teran
					},
					success(res) {
						var seron = res.data.data
						var entin = res.data.data.bill_records
						that.entin = entin
						if (entin.length == 0) {
							that.flag = true
						} else {
							that.flag = false
						}
						that.profit = seron.month_bill
					}
				})
			}
		},
		bindChange(e) {
			const val = e.detail.value
			this.year = this.years[val[0]]
			this.month = this.months[val[1]]
			this.day = this.days[val[2]]
		},
		linkToTransfer: debounce(function() {
			uni.navigateTo({
				url: '../../my/transfer/transfer?bar=' + this.ber + '&fee=' + this.fee,
			})
		}, 500, true),
		btn() {
			this.linkToTransfer()
		},
		btnCoin() {
			uni.showToast({
				title: '此功能暂未开放',
				duration: 2000,
				icon: 'none'
			})
		},
		optionTap(e) {
			var that = this;
			let Index = e.currentTarget.dataset.index; //获取点击的下拉列表的下标
			var time = this.selectData[Index];
			this.index = Index,
				this.selectShow = !this.selectShow
			uni.request({
				url: this.url + 'month/profit/',
				method: 'GET',
				data: {
					month: that.selectData[that.index]
				},
				header: {
					Authorization: 'JWT' + ' ' + this.global_.token
				},
				success(res) {}
			})
		},
		DateChange(e) {
			var that = this
			that.date = e
			uni.request({
				url: this.url + 'assets/month/profit/',
				method: 'GET',
				header: {
					Authorization: 'JWT' + ' ' + this.global_.token
				},
				data: {
					month: e
				},
				success(res) {
					var seront = res.data.data
					var ention = res.data.data.profit_records.reverse()
					console.log(ention)
					that.ention = ention
					if (ention.length == 0) {
						that.flag = true
					} else {
						that.flag = false
					}
					that.month_profit = seront.month_profit
				}
			})
		},
		DateChang(e) {
			var that = this
			this.date1 = e
			uni.request({
				url: this.url + 'assets/month/bill/',
				method: 'GET',
				header: {
					Authorization: 'JWT' + ' ' + this.global_.token
				},
				data: {
					month: e
				},
				success(res) {
					var seron = res.data.data
					var entin = res.data.data.bill_records
					that.entin = entin
					if (entin.length == 0) {
						that.flag = true
					} else {
						that.flag = false
					}
					that.profit = seron.month_bill
				}
			})
		},

	}
}
